@import "@/assets/global.scss";

























































































































































































#login {
  width: 100%;
  height: 46px;
  background: #FCA311;
  border-radius: 6px;
  line-height: 46px;
  color: #FFFFFF;
  cursor: pointer;
  user-select: unset;
}

#register {
  @extend .button;
}

#facebook {
  @extend #login;
  background: #4267B2;
}

#google {
@extend #login;
  background: #FFFFFF;
  color: rgba(0, 0, 0, 0.8);
}

.disable-btn {
  background: #D2D2D2!important;
}
